'use client'

import React, { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent } from '@/components/ui/card'

interface SlideData {
  id: number
  title: string
  subtitle: string
  description: string
  backgroundGradient: string
  ctaText: string
  ctaLink: string
}

const HeroSlider = () => {
  const [currentSlide, setCurrentSlide] = useState(0)
  const [isAutoPlaying, setIsAutoPlaying] = useState(true)

  const slides: SlideData[] = [
    {
      id: 1,
      title: "Selamat Datang di Dinas Perhubungan",
      subtitle: "Kabupaten Barru",
      description: "Melayani masyarakat dalam bidang transportasi dan perhubungan dengan profesional, transparan, dan akuntabel untuk kemajuan Kabupaten Barru.",
      backgroundGradient: "from-blue-600 via-blue-700 to-blue-800",
      ctaText: "Jelajahi Layanan",
      ctaLink: "/layanan"
    },
    {
      id: 2,
      title: "Layanan Perizinan Online",
      subtitle: "Mudah, Cepat, dan Terpercaya",
      description: "Dapatkan berbagai perizinan transportasi secara online dengan proses yang mudah dan transparan. Hemat waktu dan tenaga Anda.",
      backgroundGradient: "from-emerald-600 via-emerald-700 to-emerald-800",
      ctaText: "Ajukan Perizinan",
      ctaLink: "/layanan/perizinan-angkutan"
    },
    {
      id: 3,
      title: "Informasi Kondisi Jalan",
      subtitle: "Real-time & Akurat",
      description: "Pantau kondisi jalan terkini di Kabupaten Barru untuk perjalanan yang aman dan nyaman. Informasi diperbarui secara berkala.",
      backgroundGradient: "from-amber-600 via-amber-700 to-amber-800",
      ctaText: "Lihat Kondisi Jalan",
      ctaLink: "/informasi/kondisi-jalan"
    }
  ]

  useEffect(() => {
    if (!isAutoPlaying) return

    const interval = setInterval(() => {
      setCurrentSlide((prev) => (prev + 1) % slides.length)
    }, 5000)

    return () => clearInterval(interval)
  }, [isAutoPlaying, slides.length])

  const goToSlide = (index: number) => {
    setCurrentSlide(index)
    setIsAutoPlaying(false)
    setTimeout(() => setIsAutoPlaying(true), 10000)
  }

  const nextSlide = () => {
    setCurrentSlide((prev) => (prev + 1) % slides.length)
    setIsAutoPlaying(false)
    setTimeout(() => setIsAutoPlaying(true), 10000)
  }

  const prevSlide = () => {
    setCurrentSlide((prev) => (prev - 1 + slides.length) % slides.length)
    setIsAutoPlaying(false)
    setTimeout(() => setIsAutoPlaying(true), 10000)
  }

  return (
    <div className="relative h-screen overflow-hidden">
      {/* Slides Container */}
      <div className="relative h-full">
        {slides.map((slide, index) => (
          <div
            key={slide.id}
            className={`absolute inset-0 transition-all duration-1000 ease-in-out ${
              index === currentSlide 
                ? 'opacity-100 translate-x-0' 
                : index < currentSlide 
                  ? 'opacity-0 -translate-x-full' 
                  : 'opacity-0 translate-x-full'
            }`}
          >
            <div className={`h-full bg-gradient-to-br ${slide.backgroundGradient} relative`}>
              {/* Animated Background Pattern */}
              <div className="absolute inset-0 opacity-10">
                <div className="absolute top-20 left-20 w-32 h-32 border-4 border-white rounded-full animate-spin-slow"></div>
                <div className="absolute top-40 right-32 w-24 h-24 border-4 border-white border-t-transparent rounded-full animate-spin"></div>
                <div className="absolute bottom-32 left-1/3 w-16 h-16 border-4 border-white border-r-transparent rounded-full animate-spin-slow"></div>
                <div className="absolute bottom-20 right-20 w-20 h-20 border-4 border-white border-b-transparent rounded-full animate-spin"></div>
              </div>

              {/* Content */}
              <div className="relative z-10 h-full flex items-center">
                <div className="container mx-auto px-4">
                  <div className="max-w-4xl">
                    {/* Animated Phinisi+Gear Logo */}
                    <div className="mb-8 flex items-center space-x-4">
                      <div className="relative">
                        <div className="w-20 h-20 relative">
                          <div className="absolute inset-0 animate-spin-slow">
                            <div className="w-full h-full rounded-full border-6 border-amber-400 border-t-transparent"></div>
                          </div>
                          <div className="absolute inset-3 flex items-center justify-center">
                            <div className="text-amber-400 text-3xl animate-pulse">‚öôÔ∏è</div>
                          </div>
                        </div>
                      </div>
                      <div className="text-white">
                        <div className="text-2xl font-bold animate-fade-in-up">üö¢</div>
                        <div className="text-sm opacity-80">Phinisi Spirit</div>
                      </div>
                    </div>

                    {/* Text Content */}
                    <div className="space-y-6 animate-fade-in-up">
                      <h1 className="text-5xl md:text-7xl font-poppins font-bold text-white leading-tight">
                        {slide.title}
                      </h1>
                      <h2 className="text-2xl md:text-3xl font-medium text-amber-200">
                        {slide.subtitle}
                      </h2>
                      <p className="text-xl text-blue-100 max-w-2xl leading-relaxed">
                        {slide.description}
                      </p>
                      <div className="pt-4">
                        <Button
                          size="lg"
                          className="bg-amber-500 hover:bg-amber-600 text-white font-semibold px-8 py-4 text-lg rounded-full shadow-lg hover:shadow-xl transition-all duration-300 transform hover:scale-105"
                        >
                          {slide.ctaText}
                        </Button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Navigation Arrows */}
      <button
        onClick={prevSlide}
        className="absolute left-6 top-1/2 transform -translate-y-1/2 z-20 bg-white/20 hover:bg-white/30 backdrop-blur-sm rounded-full p-3 transition-all duration-300 group"
        aria-label="Previous slide"
      >
        <div className="w-6 h-6 text-white group-hover:scale-110 transition-transform">
          ‚Üê
        </div>
      </button>
      <button
        onClick={nextSlide}
        className="absolute right-6 top-1/2 transform -translate-y-1/2 z-20 bg-white/20 hover:bg-white/30 backdrop-blur-sm rounded-full p-3 transition-all duration-300 group"
        aria-label="Next slide"
      >
        <div className="w-6 h-6 text-white group-hover:scale-110 transition-transform">
          ‚Üí
        </div>
      </button>

      {/* Slide Indicators */}
      <div className="absolute bottom-8 left-1/2 transform -translate-x-1/2 z-20">
        <div className="flex space-x-3">
          {slides.map((_, index) => (
            <button
              key={index}
              onClick={() => goToSlide(index)}
              className={`w-3 h-3 rounded-full transition-all duration-300 ${
                index === currentSlide 
                  ? 'bg-amber-400 scale-125' 
                  : 'bg-white/50 hover:bg-white/70'
              }`}
              aria-label={`Go to slide ${index + 1}`}
            />
          ))}
        </div>
      </div>

      {/* Progress Bar */}
      <div className="absolute bottom-0 left-0 right-0 z-20">
        <div className="h-1 bg-white/20">
          <div 
            className="h-full bg-amber-400 transition-all duration-300 ease-linear"
            style={{ 
              width: `${((currentSlide + 1) / slides.length) * 100}%` 
            }}
          />
        </div>
      </div>

      {/* Scroll Indicator */}
      <div className="absolute bottom-8 right-8 z-20 animate-bounce">
        <div className="text-white/70 text-center">
          <div className="text-2xl mb-2">‚Üì</div>
          <div className="text-sm">Scroll</div>
        </div>
      </div>
    </div>
  )
}

export default HeroSlider
